import './App.scss';
import React, { useState, useEffect, useRef } from 'react';
import { Switch, Route, useLocation, useHistory } from 'react-router-dom';
import {
  CreateRecipe,
  MenuPage,
  ProfilePage,
  IntroPage,
  LoginPage,
  RegisterPage,
} from './pages/main';
import { Header, BottomNav } from './compontents/layout';
import { useAuth } from './context/AuthContext';

function App() {
  const [showIntro, setShowIntro] = useState(true);
  const { currentUser } = useState(true);
  const header = useRef();
  const location = useLocation();
  const history = useHistory();

  useEffect(() => {
    location.pathname !== '/' ? setShowIntro(false) : setShowIntro(true);
    history.push('/login');
    // console.log(currentUser);
  }, [location.pathname]);

  return (
    <Switch>
      <div className='app'>
        <Header passRef={header} />

        <div className='app__content'>
          <Route path='/my-profile' exact>
            <ProfilePage />
          </Route>
          <Route path='/create-recipe'>
            <CreateRecipe header={header} />
          </Route>
          <Route path='/my-recipes'>
            <MenuPage header={header} />
          </Route>

          {showIntro && <IntroPage />}
        </div>

        <BottomNav />

        <Route path='/login'>
          <LoginPage />
        </Route>
        <Route path='/register'>
          <RegisterPage />
        </Route>
      </div>
    </Switch>
  );
}

export default App;
