import React, { useState } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import { Container, Button, TextField } from '@material-ui/core';

import { LottieAnimation, PasswordInput } from '../../compontents';

const useStyles = makeStyles((theme) => ({
  margin: {
    margin: theme.spacing(1),
  },

  textField: {
    width: '100%',
    margin: 0,
    marginBottom: '1.5rem',
  },
}));

export const RegisterPage = () => {
  const classes = useStyles();
  const [values, setValues] = useState({
    password: '',
    showPassword: false,
  });

  const handleChange = (prop) => (event) => {
    setValues({ ...values, [prop]: event.target.value });
  };

  const handleClickShowPassword = () => {
    setValues({ ...values, showPassword: !values.showPassword });
  };

  const handleMouseDownPassword = (event) => {
    event.preventDefault();
  };

  const handleSubmit = (e) => {
    e.preventDefault();
  };

  return (
    <Container className='auth auth--login'>
      <div className='auth__header'>
        <LottieAnimation url='./images/animations/cooking.json' />
        <h1 className='auth__title'>Vytvořit účet</h1>
      </div>
      <div className='auth__body'>
        <form id='login' onSubmit={handleSubmit}>
          <TextField
            className={classes.textField}
            id='outlined-basic'
            label='E-mail'
            variant='outlined'
          />
          <PasswordInput text='Heslo' />
          <PasswordInput text='Potvrdit heslo' />
          <Button
            type='submit'
            className='auth__login'
            variant='contained'
            color='primary'
          >
            Zaregistrovat se
          </Button>
        </form>
      </div>
      <div className='auth__footer'>
        <Button color='primary'>Přihlásit se</Button>
      </div>
    </Container>
  );
};
